/* Modern reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
  background: linear-gradient(145deg, #f8f9fa, #e9ecef);
  color: #1a1a1a;
  line-height: 1.4;
  min-height: 100vh;
}

/* Main container */
.app-container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 0 20px;
  height: 100vh;
}

/* Improved homepage */
.homepage {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  text-align: center;
  gap: 20px;
}

.homepage h1 {
  font-size: 28px;
  font-weight: 300;
  letter-spacing: -0.5px;
  color: #333;
  margin: 0;
}

.homepage p {
  font-size: 16px;
  color: #666;
  max-width: 400px;
  margin: 0 0 10px 0;
}

/* Button with improved styling */
.capture-button {
  background-color: #333;
  color: white;
  border: none;
  padding: 12px 28px;
  font-size: 14px;
  border-radius: 30px;
  cursor: pointer;
  transition: all 0.2s ease;
  letter-spacing: 0.5px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  position: relative;
  overflow: hidden;
  margin-top: 20px;
}

.capture-button:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(120deg, 
    rgba(255,255,255,0) 30%, 
    rgba(255,255,255,0.2) 50%, 
    rgba(255,255,255,0) 70%);
  transform: translateX(-100%);
  transition: all 0.6s ease;
}

.capture-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 10px rgba(0, 0, 0, 0.15);
}

.capture-button:hover:after {
  transform: translateX(100%);
}

.capture-button:disabled {
  background-color: #999;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

.home-button {
  background-color: #e9ecef;
  color: #333;
  border: none;
  padding: 12px 28px;
  font-size: 14px;
  border-radius: 30px;
  cursor: pointer;
  transition: all 0.2s ease;
  letter-spacing: 0.5px;
}

.home-button:hover {
  background-color: #dee2e6;
}

/* Add a decorative element to the homepage */
.homepage:before {
  content: '';
  position: absolute;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  background: linear-gradient(45deg, #e9ecef, #f8f9fa);
  top: 15%;
  right: 10%;
  z-index: -1;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.05);
}

.homepage:after {
  content: '';
  position: absolute;
  width: 150px;
  height: 150px;
  border-radius: 50%;
  background: linear-gradient(45deg, #f8f9fa, #e9ecef);
  bottom: 15%;
  left: 10%;
  z-index: -1;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.05);
}

/* Modern postcard design - Fixed height issues */
.snapshot {
  max-width: 800px;
  margin: 0 auto;
  display: flex; /* Changed from grid to flex */
  background: white;
}

/* Left side - Image */
.earth-image {
  flex: 1.2; /* Using flex instead of grid columns */
  min-height: 100%;
}

.earth-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block; /* Remove any image gaps */
}

/* Right side - Information */
.postcard-content {
  flex: 1; /* Using flex instead of grid columns */
  padding: 40px;
  display: flex;
  flex-direction: column;
  background: white;
}

.postcard-header {
  margin-bottom: 24px;
}

.current-time {
  font-size: 14px;
  color: #666;
}


.data-section {
  flex-grow: 1; /* Allow content to expand */
}

.data-item {
  margin-bottom: 16px;
  font-size: 13px;
  line-height: 1.6;
  color: #333;
}

.data-item-title {
  font-size: 11px;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  color: #666;
  margin-bottom: 4px;
}

.postcard-footer {
  margin-top: 24px;
  text-align: left;
}

.image-location,
.timestamp {
  font-size: 11px;
  color: #999;
  margin-bottom: 0px;
}

/* Action buttons */
.snapshot-actions {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 12px;
  margin-top: 24px;
}

/* Reset all button styles to be identical */
.capture-button,
.home-button {
  all: unset; /* Reset all inherited styles */
  box-sizing: border-box;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 8px 20px;
  height: 32px;
  font-size: 14px;
  font-family: inherit;
  font-weight: 400;
  cursor: pointer;
  border: none;
  margin: 0;
}

/* Only differ in colors */
.capture-button {
  background: #1a1a1a;
  color: white;
}

.home-button {
  background: #eee;
  color: #1a1a1a;
}

.capture-button:disabled {
  background: #ccc;
  cursor: not-allowed;
}

/* Mobile adjustments */
@media (max-width: 768px) {
  .snapshot {
    flex-direction: column;
  }

  .earth-image {
    aspect-ratio: 16/9;
  }

  .postcard-content {
    padding: 24px;
  }
}

.greeting {
  font-size: 16px;
  color: #1a1a1a;
  margin-top: 8px;
  font-weight: 500;
} 